<template>
	<td @click="onClickTd">{{cellData}}</td>
</template>

<script>
	import EventBus from "./EventBus";

	export default {
		components: {}, //      .
		name: 'TdComponent', //      .
		props: {
			cellData: String,
			rowIndex: Number,
			cellIndex: Number,
		}, //       .   .
	  data() {
	    return {};
	  }, //      .
		computed: {}, //       .  .
	  methods: {
			onClickTd() {
				if (this.cellData) {
					return;
				}//      .

				EventBus.$emit('clickTd', this.rowIndex, this.cellIndex);
				//   . .      .
			}, //         .
	  }, //       .      .
		beforeCreate() {}, //   
		created() {}, //    ,   
		beforeMount() {}, //   
		mounted() {}, //      
		beforeUpdate() {}, //   
		updated() {}, //         
		beforeDestroy() {}, //   
		destroyed() {}, //      
		watch: {}, //            ,     .
	};
</script>

<style scoped>
</style>