<template>
	<div>
		<p>{{turn}} .</p>
		<table-component :table-data="tableData" />
		<div v-if="winner">{{winner}} !</div>
	</div>
</template>

<script>
	import TableComponent from './TableComponent';
	import EventBus from './EventBus';

	export default {
		components: {
			TableComponent,
		}, //      .
		props: {}, //       .   .
	  data() {
	    return {
	    	tableData: [
	    		['', '', '',],
			    ['', '', '',],
			    ['', '', '',],
		    ],
		    turn: 'O',
		    winner: '',
	    };
	  }, //      .
		computed: {}, //       .  .
	  methods: {
			onClickTd(rowIndex, cellIndex) { //       .

				// this.$root.$data.tableData[this.rowIndex][this.cellIndex] = this.$root.$data.turn;
				//    .    .
				//        .   .
				// $set    .

				this.$set(this.tableData[rowIndex], cellIndex, this.turn);

				let win = false;
				if (this.tableData[rowIndex][0] === this.turn &&
					this.tableData[rowIndex][1] === this.turn &&
					this.tableData[rowIndex][2] === this.turn) {
					win = true;
				}
				if (this.tableData[0][cellIndex] === this.turn &&
					this.tableData[1][cellIndex] === this.turn &&
					this.tableData[2][cellIndex] === this.turn) {
					win = true;
				}
				if (this.tableData[0][0] === this.turn &&
					this.tableData[1][1] === this.turn &&
					this.tableData[2][2] === this.turn) {
					win = true;
				}
				if (this.tableData[0][2] === this.turn &&
					this.tableData[1][1] === this.turn &&
					this.tableData[2][0] === this.turn) {
					win = true;
				}

				if (win) { //  .
					this.winner = this.turn;
					this.turn = 'O';
					this.tableData = [['', '', '',], ['', '', '',], ['', '', '',]];
				} else { //  .
					let all = true;
					this.tableData.forEach((row) => {
						row.forEach((cell) => {
							if (!cell) {
								all = false;
							}
						});
					});

					if (all) {
						this.winner = '';
						this.turn = 'O';
						this.tableData = [['', '', '',], ['', '', '',], ['', '', '',]];
					}
					this.turn = this.turn === 'O' ? 'X' : 'O';
				}
			},
	  }, //       .      .
		beforeCreate() {}, //   
		created() {
			EventBus.$on('clickTd', this.onClickTd); //  .
		}, //    ,   
		beforeMount() {}, //   
		mounted() {}, //      
		beforeUpdate() {}, //   
		updated() {}, //         
		beforeDestroy() {}, //   
		destroyed() {}, //      
		watch: {}, //            ,     .

	};
</script>

<style>
	table {
		border-collapse: collapse;
	}

	td {
		border: 1px solid #333333;
		width: 50px;
		height: 50px;
		text-align: center;
	}
</style>